<template>
  <v-container>
    <v-row>

      <!--  -->
      <v-col>
        <v-card>
          <v-card-title class="py-8 justify-center"></v-card-title>
          <v-card-text>
            <v-data-table
              no-data-text=""
              :loading="ordersLoading"
              loading-text=""
              hide-default-footer
              :headers="headers"
              :items="getOrders"
            >
              <!--  -->
              <template v-slot:item.paymentStatus="{ item }">
                <v-chip label dark :color="getColor(item.paymentStatus)">
                  {{ item.paymentStatus }}
                </v-chip>
              </template>

              <!--  -->
              <template v-slot:item.orderDetail="{ item }">
                <v-btn
                  icon
                  color="grey darken-3"
                  @click="showDetail(item)"
                >
                  <v-icon size="18">mdi-eye</v-icon>
                </v-btn>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-col>

      <!--  -->
      <v-dialog
        v-model="dialog"
        :max-width="$vuetify.breakpoint.xsOnly ? '100%' : '700px'"
        class="ma-0"
      >
        <v-card>
          <v-card-title>
            <span></span>
            <v-spacer></v-spacer>
            <v-icon @click="closeDetail()">mdi-close</v-icon>
          </v-card-title>

          <v-card-text>
            <v-simple-table>
              <thead>
                <tr>
                  <th class="text-left"></th>
                  <th class="text-left"></th>
                  <th class="text-left"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in detail.products" :key="index">
                  <td>{{ `${item.title} / ${item.color}` }}</td>
                  <td>{{ item.quantity }}</td>
                  <td>{{ `$ ${item.price}` }}</td>
                </tr>
              </tbody>
            </v-simple-table>

            <v-divider class="mb-5"></v-divider>

            <div class="d-flex py-2">
              <v-spacer></v-spacer>
              <span class="body-1 primary--text">{{ detail.totalPrice }}</span>
            </div>

            <div>
              <p> {{ detail.recipient.name }} /  {{ detail.recipient.email }}</p>
              <p> {{ detail.recipient.phone }} /  {{ detail.recipient.address }}</p>
            </div>
          </v-card-text>
        </v-card>
      </v-dialog>

    </v-row>
  </v-container>
</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex';

export default {
  name: 'Orders',
  data() {
    return {
      // [  ]
      dialog: false,
      detail: {},
      defaultDetail: {
        products: [],
        recipient: {
          name: '',
          phone: '',
          address: '',
          email: '',
        },
        totalPrice: '',
      },
      // [  ]
      headers: [
        {
          text: 'ID',
          value: 'orderId',
          align: 'center',
          sortable: false,
        },
        {
          text: '',
          value: 'createdAt',
          align: 'center',
          sortable: false,
        },
        {
          text: '',
          value: 'paymentStatus',
          align: 'center',
          sortable: false,
        },
        {
          text: '',
          value: 'totalPrice',
          align: 'center',
          sortable: false,
        },
        {
          text: '',
          value: 'orderDetail',
          align: 'center',
          sortable: false,
        },
      ],
    };
  },

  computed: {
    ...mapGetters('user', ['getOrders']),
    ...mapState('user', ['ordersLoading']),
  },

  methods: {
    ...mapActions('user', ['bindOrdersRef']),

    // [  ]
    showDetail(item) {
      this.detail = Object.assign({}, item);
      this.dialog = true;
    },

    // [  ]
    closeDetail() {
      this.dialog = false;
      setTimeout(() => {
        this.detail = Object.assign({}, this.defaultDetail);
      }, 300);
    },

    // [  ]
    getColor(status) {
      switch (status) {
        case '':
          return 'red';
        case '':
          return 'green';
        case '':
          return 'blue';
        default:
          return 'grey';
      }
    },
  },

  created() {
    this.detail = Object.assign({}, this.defaultDetail);
    this.bindOrdersRef();
  },
};
</script>
