<template>
  <v-form
    v-model="valid"
    ref="form"
  >
    <v-container>
      <!--  -->
      <v-subheader class="pa-0"></v-subheader>

      <v-row>
        <!--  -->
        <v-col
          cols="12"
          sm="4"
        >
          <v-text-field
            single-line
            v-model="recipientName"
            :rules="rules.name"
            label=""
          ></v-text-field>
        </v-col>

        <!-- EMAIL -->
        <v-col
          cols="12"
          sm="8"
        >
          <v-text-field
            single-line
            v-model="recipientEmail"
            :rules="rules.email"
            label="E-Mail"
          ></v-text-field>
        </v-col>

        <!--  -->
        <v-col
          cols="12"
          sm="4"
        >
          <v-text-field
            single-line
            v-model="recipientPhone"
            :rules="rules.phone"
            label=""
          ></v-text-field>
        </v-col>

        <!--  -->
        <v-col
          cols="12"
          sm="8"
        >
          <v-text-field
            single-line
            v-model="recipientAddress"
            :rules="rules.address"
            label=""
          ></v-text-field>
        </v-col>

        <!--  -->
        <v-col>
          <v-checkbox
            v-model="recipientInfoUpdate"
            color="green"
            label=""
          ></v-checkbox>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'CartPayment',

  data() {
    return {
      valid: false,
      rules: {
        email: [
          v => !!v || '',
          v => /.+@.+\..+/.test(v) || '',
        ],
        name: [
          v => !!v || '',
          v => v.length <= 20 || '20',
        ],
        phone: [
          v => !Number.isNaN(Number(v)) || '',
          v => !!v || '',
        ],
        address: [
          v => !!v || '',
        ],
      },
    };
  },

  methods: {
    ...mapActions('cart', ['checkout']),

    validate() {
      if (this.$refs.form.validate()) {
        this.checkout();
      }
    },
  },

  computed: {
    recipientName: {
      get() {
        return this.$store.state.cart.recipientName;
      },
      set(value) {
        this.$store.commit('cart/setRecipientName', value);
      },
    },

    recipientEmail: {
      get() {
        return this.$store.state.cart.recipientEmail;
      },
      set(value) {
        this.$store.commit('cart/setRecipientEmail', value);
      },
    },

    recipientPhone: {
      get() {
        return this.$store.state.cart.recipientPhone;
      },
      set(value) {
        this.$store.commit('cart/setRecipientPhone', value);
      },
    },

    recipientAddress: {
      get() {
        return this.$store.state.cart.recipientAddress;
      },
      set(value) {
        this.$store.commit('cart/setRecipientAddress', value);
      },
    },

    recipientInfoUpdate: {
      get() {
        return this.$store.state.cart.recipientInfoUpdate;
      },
      set(value) {
        this.$store.commit('cart/setRecipientInfoUpdate', value);
      },
    },
  },

  created() {
    // [  ]
    this.$store.dispatch('cart/getRecipientDetail');
  },
};
</script>
