/* eslint no-shadow: ["error", { "allow": ["state"] }] */

import { vuexfireMutations, firestoreAction } from 'vuexfire';
import moment from 'moment';
import router from '@/router';
import { db, auth } from '@/db';

const state = {
  // [  ]
  loginLoading: false,
  userUID: '',
  userEmail: null,
  // [  ]
  orders: [],
  ordersLoading: false,
  // [  ]
  signUpErrorMessage: '',
};

const getters = {
  // [ timestamp ]
  getOrders(state) {
    return state.orders.map(item => ({
      products: item.products,
      orderId: item.orderId,
      paymentStatus: item.paymentStatus,
      recipient: item.recipient,
      totalPrice: `$ ${item.totalPrice}`,
      uid: item.uid,
      createdAt: moment(item.createdAt).format('YYYY-MM-DD'),
    }))
      .sort((a, b) => moment(a.createdAt).format('YYYYMMDD') - moment(b.createdAt).format('YYYYMMDD')).reverse();
  },
};

const mutations = {
  // [ vuexfire ]
  ...vuexfireMutations,

  // [  ]
  setUser(state, payload) {
    if (payload !== null) {
      state.userEmail = payload.email;
      state.userUID = payload.uid;
    } else {
      state.userEmail = null;
      state.userUID = '';
    }
  },

  setOrdersLoading(state, payload) {
    state.ordersLoading = payload;
  },

  // [ / ]
  setLoginLoading(state, payload) {
    state.loginLoading = payload;
  },

  // [  ]
  setErrorMessage(state, payload) {
    state.errorMessage = payload;
  },
};

const actions = {
  // [  ]
  autoSignIn({ commit }, payload) {
    commit('setUser', payload);
  },

  // [  ]
  userSignUp({ commit, dispatch }, payload) {
    commit('setLoginLoading', true);
    auth.createUserWithEmailAndPassword(payload.email, payload.password)
      .then((data) => {
        // 
        const ref = db.collection('users').doc(data.user.uid);
        const info = {
          email: data.user.email,
          uid: data.user.uid,
          name: '',
          address: '',
          phone: '',
          roles: 'user',
        };
        ref.set(info);
        // 
        dispatch('userLogIn', payload);
      })
      .catch((error) => {
        commit('setErrorMessage', error.message);
        console.log('code: ', error.code);
        console.log('message: ', error.message);
      });
  },

  // [  ]
  userLogIn({ commit }, payload) {
    commit('setLoginLoading', true);
    auth.signInWithEmailAndPassword(payload.email, payload.password)
      .then((data) => {
        commit('setUser', data.user);
        commit('setLoginLoading', false);
        router.push(payload.path);
      })
      .catch((error) => {
        console.log(error);
      });
  },

  // [  ]
  userLogOut({ commit }) {
    auth.signOut().then(() => {
      commit('setUser', null);
      router.push('/login');
    }).catch((error) => {
      console.log(error);
    });
  },

  // [ firebase orders ]
  bindOrdersRef: firestoreAction(({ state, commit, bindFirestoreRef }) => {
    commit('setOrdersLoading', true);
    bindFirestoreRef(
      'orders',
      db.collection('orders').where('uid', '==', state.userUID),
    ).then(() => {
      commit('setOrdersLoading', false);
    });
  }),
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
