import Vue from 'vue';
import Vuex from 'vuex';
import { vuexfireMutations, firestoreAction } from 'vuexfire';
import user from './modules/user';
import cart from './modules/cart';
import { db } from '@/db';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    user,
    cart,
  },

  state: {
    // [  ]
    products: [],
    category: [],
    productSort: 'default',
    productLoading: false,
  },

  mutations: {
    // [ vuexfire ]
    ...vuexfireMutations,

    // [  ]
    changeProductSort(state, payload) {
      state.productSort = payload;
    },

    // [  ]
    setProductLoading(state, payload) {
      state.productLoading = payload;
    },
  },

  actions: {
    // [ firebase  category  ]
    bindCategoryRef: firestoreAction(({ bindFirestoreRef }) => {
      bindFirestoreRef('category', db.collection('category'));
    }),

    // [ firebase  products  ]
    bindProductsRef: firestoreAction(({ commit, bindFirestoreRef }) => {
      commit('setProductLoading', true);
      bindFirestoreRef(
        'products',
        db.collection('products')
          .where('enable', '==', true),
      ).then(() => {
        commit('setProductLoading', false);
      });
    }),
  },
});
